@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
body {
  min-width: 320px;
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
  color: #000000;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.main-header {
  padding-top: 22px;
}
.main-header__logo {
  display: block;
  width: 86px;
  margin: 0 auto;
  margin-bottom: 22px;
}
.main-header__logo[href]:hover,
.main-header__logo[href]:focus {
  opacity: 0.6;
}
.main-header__logo[href]:active {
  opacity: 0.3;
}
.main-header__logo-image {
  display: block;
  width: 86px;
  height: 35px;
}
.main-header__logo picture {
  pointer-events: none;
}
.main-header__additional-navigation {
  display: none;
}
.main-header__shipping-text {
  display: none;
}
@media (min-width: 768px) {
  .main-header {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 0;
  }
  .main-header__logo-container {
    order: 1;
    flex-grow: 1;
    padding-top: 22px;
    padding-right: 50px;
    padding-bottom: 20px;
    padding-left: 49px;
    border-bottom: 1px solid rgba(68, 68, 68, 0.1);
  }
  .main-header__logo {
    width: auto;
    margin: 0;
  }
  .main-header__logo-image {
    width: 112px;
    height: 46px;
  }
  .main-header__user-navigation {
    order: 2;
    max-width: 72%;
  }
  .main-header__main-navigation {
    order: 3;
    box-sizing: border-box;
    width: 62%;
    padding-top: 17px;
    padding-left: 49px;
    background-color: #f1f1f1;
  }
  .main-header__main-navigation--index-page {
    background-color: #f9f9f9;
  }
  .main-header__container {
    order: 4;
    width: 38%;
    background-color: #f1f1f1;
  }
  .main-header__container--index-page {
    background-color: #f9f9f9;
  }
  .main-header__shipping-text {
    display: block;
    margin: 0;
    padding-top: 17px;
    padding-right: 50px;
    font-size: 15px;
    font-weight: 400;
    color: #444444;
    text-align: right;
  }
}
@media (min-width: 1150px) {
  .main-header__logo-container {
    order: 2;
    flex-grow: 0;
    padding: 0;
    padding-top: 9px;
    padding-right: 101px;
    padding-left: 40px;
    border-bottom: none;
  }
  .main-header__logo-image {
    width: 149px;
    height: 72px;
  }
  .main-header__user-navigation {
    order: 3;
    width: 385px;
    margin-right: auto;
  }
  .main-header__main-navigation {
    order: 1;
    display: flex;
    flex-grow: 0;
    width: 445px;
    margin-left: auto;
    padding-top: 0;
    padding-left: 0;
    background-color: transparent;
  }
  .main-header__container {
    order: 4;
    display: flex;
    width: 100%;
    border-top: 1px solid rgba(68, 68, 68, 0.1);
  }
  .main-header__additional-navigation {
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;
    width: 750px;
  }
  .main-header__shipping-text {
    box-sizing: border-box;
    width: 400px;
    margin-right: auto;
  }
}
.main-navigation {
  position: relative;
}
.main-navigation--opened .main-navigation__toggle {
  background-image: url("../img/icon-menu-close.svg");
  background-position: 0 0;
}
.main-navigation--closed .main-navigation__toggle {
  background-image: url("../img/icon-menu-open.svg");
  background-position: 0 3px;
}
.main-navigation__toggle {
  position: absolute;
  top: -50px;
  right: 30px;
  width: 21px;
  height: 20px;
  background-color: transparent;
  background-repeat: no-repeat;
  border: none;
}
.main-navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  border-top: 1px solid #ececec;
}
.main-navigation__item {
  border-bottom: 1px solid #ececec;
}
.main-navigation__link {
  display: block;
  padding-top: 26px;
  padding-bottom: 28px;
  color: #000000;
}
.main-navigation__link:hover,
.main-navigation__link:focus {
  opacity: 0.6;
}
.main-navigation__link:active {
  opacity: 0.3;
}
.main-navigation__link-text {
  display: block;
  box-sizing: border-box;
  width: 260px;
  margin: 0 auto;
  padding-left: 45px;
  padding-right: 30px;
  pointer-events: none;
}
@media (max-width: 767px) {
  .main-navigation--closed .main-navigation__item {
    display: none;
  }
  .main-navigation--closed + .user-navigation {
    display: none;
  }
  .main-navigation--no-js .main-navigation__item {
    display: block;
  }
  .main-navigation--no-js + .user-navigation {
    display: block;
  }
  .main-navigation--no-js .main-navigation__toggle {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-navigation {
    font-size: 15px;
  }
  .main-navigation__toggle {
    display: none;
  }
  .main-navigation__list {
    display: flex;
    flex-wrap: wrap;
    border-top: none;
  }
  .main-navigation__item {
    margin-right: 36px;
    border-bottom: none;
  }
  .main-navigation__link {
    padding: 0;
  }
  .main-navigation__link-text {
    display: block;
    box-sizing: border-box;
    width: auto;
    margin: 0 auto;
    padding-left: 0;
    padding-right: 0;
  }
}
@media (min-width: 1150px) {
  .main-navigation {
    font-size: 17px;
  }
  .main-navigation__item {
    margin-right: 0;
    padding-top: 31px;
    padding-right: 36px;
    padding-bottom: 33px;
    padding-left: 34px;
  }
  .main-navigation__item:first-child {
    border-right: 1px solid rgba(68, 68, 68, 0.1);
  }
}
.user-navigation__link {
  display: block;
  padding-top: 26px;
  padding-bottom: 28px;
  color: #000000;
  border-bottom: 1px solid #ececec;
}
.user-navigation__link:hover .user-navigation__icon,
.user-navigation__link:focus .user-navigation__icon {
  fill: #63d1bb;
  opacity: 1;
}
.user-navigation__link:active .user-navigation__icon {
  opacity: 0.3;
}
.user-navigation__link:hover .user-navigation__cart-text,
.user-navigation__link:focus .user-navigation__cart-text {
  opacity: 0.6;
}
.user-navigation__link:active .user-navigation__cart-text {
  opacity: 0.3;
}
.user-navigation__link-text {
  position: relative;
  display: block;
  box-sizing: border-box;
  width: 260px;
  margin: 0 auto;
  padding-left: 45px;
  padding-right: 30px;
  pointer-events: none;
}
.user-navigation__icon {
  position: absolute;
  fill: #231f20;
  opacity: 0.2;
}
.user-navigation__icon--search {
  top: 5px;
  left: 2px;
}
.user-navigation__icon--cart {
  top: 4px;
  left: 0;
}
@media (min-width: 768px) {
  .user-navigation {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    border-bottom: 1px solid rgba(68, 68, 68, 0.1);
  }
  .user-navigation__link {
    box-sizing: border-box;
    padding: 0;
    border-bottom: none;
  }
  .user-navigation__link--search {
    width: 84px;
    border-right: 1px solid rgba(68, 68, 68, 0.1);
    border-left: 1px solid rgba(68, 68, 68, 0.1);
  }
  .user-navigation__link--cart {
    width: 314px;
    padding-top: 31px;
    padding-right: 50px;
    padding-bottom: 31px;
    padding-left: 31px;
  }
  .user-navigation__link-text {
    width: auto;
    padding-right: 0;
  }
  .user-navigation__link-text--search {
    padding-left: 0;
  }
  .user-navigation__link-text--cart {
    padding-left: 51px;
  }
  .user-navigation__icon--search {
    top: 36px;
    left: 33px;
  }
  .user-navigation__search-text {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}
@media (min-width: 1150px) {
  .user-navigation {
    border-bottom: none;
  }
  .user-navigation__link--cart {
    width: 301px;
    padding-right: 35px;
    padding-bottom: 33px;
  }
}
.additional-navigation {
  margin: 0;
  padding: 0;
  list-style: none;
  margin-left: auto;
  padding-top: 17px;
  padding-left: 50px;
  font-size: 15px;
}
.additional-navigation__item {
  margin-right: 61px;
}
.additional-navigation__link {
  color: #000000;
}
.additional-navigation__link:hover,
.additional-navigation__link:focus {
  opacity: 0.6;
}
.additional-navigation__link:active {
  opacity: 0.3;
}
.main-content__index-background {
  box-sizing: border-box;
  min-height: 282px;
  padding-top: 70px;
  padding-bottom: 100px;
  background-color: #f9f9f9;
  background-image: url("../img/hero-mobile@1x.jpg");
  background-position: 50% -19px;
  background-repeat: no-repeat;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .main-content__index-background {
    background-image: url("../img/hero-mobile@2x.jpg");
    background-size: 575px auto;
  }
}
.main-content__title-container {
  padding-top: 70px;
  padding-bottom: 76px;
  background-color: #f1f1f1;
}
.main-content__title {
  max-width: 315px;
  margin: 0 auto;
  padding-right: 30px;
  padding-left: 30px;
  font-size: 27px;
  line-height: 30px;
  text-align: center;
}
.main-content__title--index-page {
  color: #010000;
}
@media (min-width: 768px) {
  .main-content__index-background {
    min-height: 585px;
    padding-top: 112px;
    background-image: url("../img/hero-tablet@1x.jpg");
    background-position: 50% -42px;
  }
  .main-content__title-container {
    position: relative;
    padding-top: 54px;
    padding-bottom: 90px;
  }
  .main-content__title-container::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 22px;
    background-image: url("../img/bg-zigzag-fill.svg");
    background-position: 0 0;
    background-repeat: repeat-x;
    background-size: cover;
  }
  .main-content__title {
    display: block;
    box-sizing: border-box;
    width: 768px;
    margin: 0 auto;
    padding-left: 50px;
    padding-right: 50px;
    max-width: none;
    font-size: 43px;
    line-height: 50px;
  }
  .main-content__title--index-page {
    color: #000000;
  }
}
@media (min-width: 768px) and (min-resolution: 144dpi), (min-width: 768px) and (min-resolution: 1.5dppx) {
  .main-content__index-background {
    background-image: url("../img/hero-tablet@2x.jpg");
    background-size: 1150px auto;
  }
}
@media (min-width: 1150px) {
  .main-content__index-background {
    background-image: url("../img/hero-desktop@1x.jpg");
  }
  .main-content__title-container {
    padding-top: 52px;
    padding-bottom: 109px;
  }
  .main-content__title {
    font-size: 53px;
    line-height: 57px;
  }
}
@media (min-width: 1150px) and (min-resolution: 144dpi), (min-width: 1150px) and (min-resolution: 1.5dppx) {
  .main-content__index-background {
    background-image: url("../img/hero-desktop@2x.jpg");
    background-size: 1150px auto;
  }
}
.catalog-sections {
  font-size: 19px;
}
.catalog-sections__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}
.catalog-sections__link {
  position: relative;
  display: block;
  padding-top: 25px;
  padding-right: 160px;
  padding-bottom: 30px;
  padding-left: 28px;
  color: #ffffff;
}
.catalog-sections__link::after {
  content: "";
  position: absolute;
